
/* eslint-disable indent,camelcase */

import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import UtilMixin from '@/mixins/UtilMixin.vue'
import UploadImg from '@/components/uploadImg/uploadImg.vue'
import FormMixin from '@/mixins/FormMixin.vue'

export default {
mixins: [UtilMixin, FormMixin],
data() {
return {
groupColor: '#50b97b',
name: null,
tel: null,
qq: null,
identity: null,
want_blance: null,
idcard_pic: '',
idcard_backpic: '',
idcard_people_pic: '',
type: 0
}
},
components: {
XInput,
Group,
XButton,
Box,
Flexbox,
FlexboxItem,
UploadImg
},
computed: {},
methods: {
changeImg(data, nodelName) {
console.log(nodelName)
if (nodelName !== 'undefined') {
this[nodelName] = data
}
console.log(this[nodelName])
},
submit() {
this.formMixin_submit('/yunhu/customer_update/')
.then(() => {
this.$router.push({name: 'Index'})
})
.catch((err) => {
console.log(`code:${err.code} \n msg:${err.msg}`)
})
}
},
mounted() {
}
}



